body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-header {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  text-align: center;
  background-color: #13005a;
  color: white;
  justify-content: center;
  align-items: center;
}

.heading-text {
  color: #f9f9f9;
  font-family: cursive;
  font-size: 42px;
  font-weight: bolder;
  text-align: center;
  margin: 50px;
}

.row {
  display: flex;
  flex-direction: row;
}

.column {
  display: flex;
  flex-direction: column;
}

.jc-center {
  justify-content: center;
}

.square {
  min-width: 65px;
  min-height: 65px;
  font-size: 64px;
  text-align: center;
  color: #1cdbf5;
  font-weight: bolder;
  padding: 30px;
}

.b-bottom-right {
  border-right: 15px solid #00fff6;
  border-bottom: 15px solid #00fff6;
}

.b-bottom {
  border-bottom: 15px solid #00fff6;
}

.b-right {
  border-right: 15px solid #00fff6;
}

.clear-button {
  font-family: cursive;

  width: max-content;
  margin: 40px auto 10px auto;
}

.fc-aqua {
  font-family: cursive;
  color: #1cdbf5;
}

.fc-white {
  font-family: cursive;
  color: white;
}
